
.popup {
  width: 450px;
  top: -45%;
  visibility: hidden;
  border-radius: 5px;
  padding: 13px 17px 20px;
  background-color: #fff;
  border-top: 3px solid rgba(52, 87, 220, 0.3);
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-top: 3px solid #ea4d67;
  z-index: 10;
  transition: all 0.5s ease;
}
.popup.show {
  top: 0;
  visibility: visible;
}
.popup.online {
  border-color: #2ecc71;
}
.popup .icon i {
  width: 40px;
  color: var(--color-name);
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-right: 15px;
  font-size: 30px;
}

.popup .title {
  font-size: 1.2rem;
}
.popup .desc {
  color: #474747;
  margin: 3px 0px 10px;
  font-size: 1.04rem;
}
.popup .reconnect {
  border: none;
  outline: none;
  color: #fff;
  padding: 9px 15px;
  background-color: #5372f0;
  cursor: pointer;
  font-weight: 500;
  border-radius: 4px;
  margin-top: 5px;
}

.popup.online .reconnect {
  background-color: #bfbfbf;
  pointer-events: none;
}

@media screen and (max-width: 550px) {
    .popup{
        width: 100%;
        padding: 10px 15px 17px;
    }
}
